<template>
    <div class="wp">
        <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="textarea1"> </el-input>

        <el-button-group>
            <el-button type="primary" icon="el-icon-refresh" @click="conversion">转换</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="reset">清空</el-button>
            <el-switch v-model="border" active-text="添加边框" class="m-l-20 m-t-5"></el-switch>

            <el-button type="primary" @click="enlarge">放大</el-button>
            <el-button type="primary" @click="narrow">缩小</el-button>
        </el-button-group>

        <img ref="imgRef" :src="imgBase64" alt="" srcset="" class="img-cls" :class="{ 'border-cls': border }" />
    </div>
</template>

<script>
export default {
    data() {
        return {
            textarea1: '',
            imgBase64: '',
            border: false,
            rate: 1,
        };
    },
    methods: {
        reset() {
            this.textarea1 = '';
            this.imgBase64 = '';
            this.rate = 1;
            this.$refs.imgRef.style.transform = `scale(1)`;
        },
        conversion() {
            this.textarea1 = this.textarea1.replace(/['"]/g, '');
            this.imgBase64 = this.textarea1;
        },
        // 放大
        enlarge() {
            this.rate += 0.1;
            this.$refs.imgRef.style.transform = `scale(${this.rate})`;
        },
        // 缩小
        narrow() {
            this.rate -= 0.1;
            this.$refs.imgRef.style.transform = `scale(${this.rate})`;
        },
    },
};
</script>

<style lang="scss" scoped>
.wp {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
}

.border-cls {
    border: 1px solid #f00;
}

.img-cls {
    transform-origin: left top;
    transition: all linear 0.1s;
}
</style>
